	section.our-services{   
		background-color: #f9f4f4;
		.header-services {
			margin: 0 auto;
			font-size: 20px;
			margin-bottom: 25px;
			text-align: center;

			@include bp-medium {
				text-align: left;
				margin-left: rem(120px, $text-bp-md);
				margin-bottom: rem(75px, $text-bp-md);
			}
			@include bp-large {
				text-align: left;
				margin-left: rem(120px, $text-bp-lg);
				margin-bottom: rem(75px, $text-bp-lg);
			}
		}
		// Make space in between columns thinner? change color of border
		// should be 2px
		.container{
			display: flex;
			width: 100%;
			flex-direction: column;
			@include bp-medium {
				width: 111%;
				flex-direction: row;
		        margin-left: rem(-60, $text-bp-md);
		        margin-right: rem(-60, $text-bp-md);
		      }
		      @include bp-large {
		      	width: 110%;
		        margin-left: rem(-60, $text-bp-lg);
		        margin-right: rem(-60, $text-bp-lg);
		      }
		}
		// Move numbers to the left and thin out font. Move text down.
		.item{
			color: $white;
			text-align: left;
			border: 1px solid #d3d3d3;

			height: 480px;
			font-size: 18px;
			@include bp-medium {
				flex-basis: 25%;

				height: rem(480px, $text-bp-md);
				font-size: rem(18px, $text-bp-md);
			}
			@include bp-large {
				height: rem(480px, $text-bp-lg);
				font-size: rem(18px, $text-bp-lg);
			}
			.numb {
				color: rgba(255, 255, 255, 0.6);
			}
			.ctn {
				width: 100%;
				height: 100%;
				background:#2c2424;
			}
			p {
				margin: 0;
				padding: 15%;
			};
		}
		@keyframes bg {
			from {
				background-color: #2c2424;
			}
			to {
				background-color: rgba(0,0,0,0.3);
			}
		}
		.item.one:hover {
			background: url('/img/rollover-1.jpg');
			background-size: 100% auto;
			.ctn.one {
				animation-name: bg;
				animation-duration: 0.5s;
				background-color: rgba(0,0,0,0.3);
			}
		}
		.item.two:hover {
			background: url('/img/rollover-2.jpg');
			background-size: 100% auto;
			.ctn.two {
				animation-name: bg;
				animation-duration: 0.5s;
				background-color: rgba(0,0,0,0.3);
			}
		}
		.item.three:hover {
			background: url('/img/rollover-3.jpg');
			background-size: 100% auto;
			.ctn.three {
				animation-name: bg;
				animation-duration: 0.5s;
				background-color: rgba(0,0,0,0.3);
			}
		}
		.item.four:hover {
			background: url('/img/rollover-4.jpg');
			background-size: 100% auto;
			.ctn.four {
				animation-name: bg;
				animation-duration: 0.5s;
				background-color: rgba(0,0,0,0.3);
			}
		}

	}
